"use strict";

var BeforeInputEventPlugin = require(6);
var ChangeEventPlugin = require(10);
var ClientReactRootIndex = require(11);
var CompositionEventPlugin = require(12);
var DefaultEventPluginOrder = require(17);
var EnterLeaveEventPlugin = require(18);
var ExecutionEnvironment = require(25);
var HTMLDOMPropertyConfig = require(26);
var MobileSafariClickEventPlugin = require(29);
var ReactBrowserComponentMixin = require(33);
var ReactComponentBrowserEnvironment =
  require(37);
var ReactDefaultBatchingStrategy = require(52);
var ReactDOMComponent = require(43);
var ReactDOMButton = require(42);
var ReactDOMForm = require(44);
var ReactDOMImg = require(46);
var ReactDOMInput = require(47);
var ReactDOMOption = require(48);
var ReactDOMSelect = require(49);
var ReactDOMTextarea = require(51);
var ReactEventListener = require(61);
var ReactInjection = require(62);
var ReactInstanceHandles = require(64);
var ReactMount = require(67);
var SelectEventPlugin = require(85);
var ServerReactRootIndex = require(86);
var SimpleEventPlugin = require(87);
var SVGDOMPropertyConfig = require(84);

var createFullPageComponent = require(107);

function inject() {
  ReactInjection.EventEmitter.injectReactEventListener(
    ReactEventListener
  );

  /**
   * Inject modules for resolving DOM hierarchy and plugin ordering.
   */
  ReactInjection.EventPluginHub.injectEventPluginOrder(DefaultEventPluginOrder);
  ReactInjection.EventPluginHub.injectInstanceHandle(ReactInstanceHandles);
  ReactInjection.EventPluginHub.injectMount(ReactMount);

  /**
   * Some important event plugins included by default (without having to require
   * them).
   */
  ReactInjection.EventPluginHub.injectEventPluginsByName({
    SimpleEventPlugin: SimpleEventPlugin,
    EnterLeaveEventPlugin: EnterLeaveEventPlugin,
    ChangeEventPlugin: ChangeEventPlugin,
    CompositionEventPlugin: CompositionEventPlugin,
    MobileSafariClickEventPlugin: MobileSafariClickEventPlugin,
    SelectEventPlugin: SelectEventPlugin,
    BeforeInputEventPlugin: BeforeInputEventPlugin
  });

  ReactInjection.NativeComponent.injectGenericComponentClass(
    ReactDOMComponent
  );

  ReactInjection.NativeComponent.injectComponentClasses({
    'button': ReactDOMButton,
    'form': ReactDOMForm,
    'img': ReactDOMImg,
    'input': ReactDOMInput,
    'option': ReactDOMOption,
    'select': ReactDOMSelect,
    'textarea': ReactDOMTextarea,

    'html': createFullPageComponent('html'),
    'head': createFullPageComponent('head'),
    'body': createFullPageComponent('body')
  });

  // This needs to happen after createFullPageComponent() otherwise the mixin
  // gets double injected.
  ReactInjection.CompositeComponent.injectMixin(ReactBrowserComponentMixin);

  ReactInjection.DOMProperty.injectDOMPropertyConfig(HTMLDOMPropertyConfig);
  ReactInjection.DOMProperty.injectDOMPropertyConfig(SVGDOMPropertyConfig);

  ReactInjection.EmptyComponent.injectEmptyComponent('noscript');

  ReactInjection.Updates.injectReconcileTransaction(
    ReactComponentBrowserEnvironment.ReactReconcileTransaction
  );
  ReactInjection.Updates.injectBatchingStrategy(
    ReactDefaultBatchingStrategy
  );

  ReactInjection.RootIndex.injectCreateReactRootIndex(
    ExecutionEnvironment.canUseDOM ?
      ClientReactRootIndex.createReactRootIndex :
      ServerReactRootIndex.createReactRootIndex
  );

  ReactInjection.Component.injectEnvironment(ReactComponentBrowserEnvironment);

  if ("production" !== "production") {
    var url = (ExecutionEnvironment.canUseDOM && window.location.href) || '';
    if ((/[?&]react_perf\b/).test(url)) {
      var ReactDefaultPerf = require(54);
      ReactDefaultPerf.start();
    }
  }
}

module.exports = {
  inject: inject
};
